import React, { useState } from 'react';
import styled from 'styled-components';
import { ContactMail } from 'styled-icons/material/ContactMail';

const Container = styled.div`
  position: relative;
  padding: 2rem;
  overflow: hidden;
  font-size: 1.5rem;
  margin-left: 3rem;
  margin: 0 auto;
  @media (min-width: 768px) {
    width: 750px;
  }

  @media (min-width: 992px) {
    width: 970px;
  }

  @media (min-width: 1200px) {
    width: 1100px;
  }
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
`;
const FormStyles = styled.form`
  display: grid;
  text-align: left;
  justify-content: flex-start;

  input,
  textarea {
    margin-bottom: 1rem;
    border-radius: 0.2rem;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    box-shadow: none;
    border: 1px solid;
    ::placeholder {
      font-size: 1rem;
      font-family: 'Rambla';
    }
  }
  textarea {
    font-size: 1rem;
    width: 40vw;
    height: 17vh;
    font-family: 'roboto';
    resize: none;
  }

  button {
    background-color: ${({ theme }) => theme.colors.primaryLinks};
    border: 1px solid black;
    border-radius: 4px;
    padding: 0.5rem 0.5rem;
    color: white;
    transition: 0.3s ease-in-out;
    &:hover {
      background-color: ${({ theme }) => theme.colors.primaryHover};
      transform: translateY(-3px);
      box-shadow: 5px 5px 10px rgba(0, 50, 50, 0.5);
    }
  }
  @media screen and (max-width: 780px) {
    margin: 0;
    width: 80%;
    h1 {
      font-size: 1.5rem;
    }
  }
`;
const Profile = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  float: right !important;
  img {
    width: 400px;
    height: auto;
    display: block;
    max-width: 100%;
  }
  div {
    top: 0;
    z-index: 1;
    position: absolute;
    border-style: solid;
    border-width: 100vw 0 0 20vw;
    border-color: rgba(223, 50, 50, 0) transparent transparent
      ${({ theme }) => theme.colors.primaryLight};
  }
`;
const Card = styled.div`
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  position: relative;
  margin: 8px 0;
  width: 100%;
  overflow: hidden;
  -webkit-transition: height 1s, color 1s, margin 1s;
  transition: height 1s, color 1s, margin 1s;
  z-index: 1;
  background: ${({ theme }) => theme.colors.primaryHover};
`;

const Form = () => {
  const [status, setStatus] = useState({
    submitted: false,
    submitting: false,
    info: { error: false, msg: null },
  });

  const [inputs, setInputs] = useState({
    name: '',
    email: '',
    message: '',
  });

  const handleServerResponse = (ok, msg) => {
    if (ok) {
      setStatus({
        submitted: true,
        submitting: false,
        info: { error: false, msg },
      });

      setInputs({
        name: '',
        email: '',
        message: '',
      });
    } else {
      setStatus({
        info: { error: true, msg },
      });
    }
  };

  const handleOnChange = e => {
    e.persist();
    setInputs(prev => ({
      ...prev,
      [e.target.id]: e.target.value,
    }));
    setStatus({
      submitted: false,
      submitting: false,
      info: { error: false, msg: null },
    });
  };

  const handleOnSubmit = e => {
    e.preventDefault();
    setStatus(prevStatus => ({ ...prevStatus, submitting: true }));
    // axios({
    //   method: 'POST',
    //   url: 'https://formspree.io/meqqwebb',
    //   data: inputs,
    // })
    //   .then(response => {
    //     handleServerResponse(true, 'Thank you for your message!');
    //   })
    //   .catch(error => {
    //     handleServerResponse(false, error.response.data.error);
    //   });
  };
  return (
    <>
      <h1>
        <ContactMail size="60" /> Contactame y formare parte de tu equipo!
      </h1>
      <Container>
        <FormStyles onSubmit={handleOnSubmit}>
          <label htmlFor="name">Nombre</label>
          <input
            required
            value={inputs.name}
            id="name"
            onChange={handleOnChange}
            type="text"
            name="name"
            placeholder="Nombre"
          />
          <label htmlFor="email">Correo</label>
          <input
            required
            value={inputs.email}
            id="email"
            onChange={handleOnChange}
            type="email"
            name="_replyto"
            placeholder="name@example.com"
          />
          <label htmlFor="message">Message</label>
          <textarea
            required
            value={inputs.message}
            id="message"
            onChange={handleOnChange}
            name="message"
            cols="30"
            placeholder="Describe aqui tu mensaje"
            rows="10"
          ></textarea>
          <button type="submit" disabled={status.submitting}>
            {!status.submitting
              ? !status.submitted
                ? 'Envia tu mensaje'
                : 'Enviado'
              : 'Enviando, por favor espere..'}
          </button>

          {status.info.error && <div className="error">Error: {status.info.msg}</div>}
          {!status.info.error && status.info.msg && <p>{status.info.msg}</p>}
        </FormStyles>
        <Profile>
          <img src="/images/contact.png" alt="Image profile" />
          <div></div>
        </Profile>
      </Container>
    </>
  );
};

export default Form;
